// src/app/dashboard/randevular/page.tsx

'use client'; 

import { useEffect, useState } from 'react'; 
import { format } from 'date-fns';
import { tr } from 'date-fns/locale';
import { UserCircle, Phone, Mail, Check, X, CheckCircle } from 'lucide-react'; 
import DashboardHeader from '@/components/dashboard/DashboardHeader'; 

// 1. Status Enum'u (Backend ile aynı olmalı)
enum AppointmentStatus {
  PENDING = 'pending',
  CONFIRMED = 'confirmed',
  CANCELLED = 'cancelled',
  COMPLETED = 'completed',
}

// 2. Appointment Tipi (Backend Entity ile aynı)
interface Appointment {
  id: string;
  name: string;
  dateTime: string; 
  phone: string;
  email: string;
  type: 'Online' | 'Yüz Yüze';
  status: AppointmentStatus; 
}

// 3. API Adresi (Portunuzu kontrol edin, 3001 veya 3002)
const API_URL = 'http://localhost:3001/appointments'; 

export default function DashboardPage() {
  
  const [appointments, setAppointments] = useState<Appointment[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // 4. Veri Çekme (Data Fetching) Fonksiyonu
  useEffect(() => {
    fetchAppointments();
  }, []); 

  const fetchAppointments = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const token = localStorage.getItem('access_token');
      if (!token) { throw new Error('Oturum bulunamadı veya geçersiz.'); }

      const response = await fetch(API_URL, {
        method: 'GET',
        headers: { 'Authorization': `Bearer ${token}` }
      });

      if (response.status === 401) {
        window.location.href = '/auth/login';
        return;
      }
      if (!response.ok) {
        throw new Error('Randevular çekilemedi.');
      }

      const data: Appointment[] = await response.json();
      setAppointments(data);
    } catch (err: unknown) {
      setError(err instanceof Error ? err.message : 'Bilinmeyen bir hata oluştu.');
    } finally {
      setIsLoading(false);
    }
  };

  // 5. Durum Güncelleme (Onayla/İptal Et) Fonksiyonu
  const handleUpdateStatus = async (id: string, newStatus: AppointmentStatus) => {
    const token = localStorage.getItem('access_token');
    if (!token) {
        setError("Oturum bulunamadı. Lütfen tekrar giriş yapın.");
        return;
    }

    // Butona tıklandığında geçici olarak UI'ı güncelle (İyimser Güncelleme)
    setAppointments(prevAppointments => 
        prevAppointments.map(app => 
            app.id === id ? { ...app, status: newStatus } : app
        )
    );

    try {
        const response = await fetch(`${API_URL}/${id}/status`, {
            method: 'PATCH',
            headers: { 
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}` 
            },
            body: JSON.stringify({ status: newStatus })
        });

        if (!response.ok) {
            throw new Error('Durum güncellenemedi.');
            // Başarısız olursa, listeyi API'den yeniden çekerek UI'ı düzelt
            // fetchAppointments(); 
        }
        
        // Başarılı olursa, UI zaten güncellendi (İyimser Güncelleme sayesinde)
        // İsteğe bağlı olarak listeyi yeniden çekebilirsiniz:
        // fetchAppointments();

    } catch (err: unknown) {
        setError(err instanceof Error ? err.message : 'Bilinmeyen bir hata oluştu.');
        // Başarısız olursa, listeyi API'den yeniden çekerek UI'ı düzelt
        fetchAppointments();
    }
  };

  // 6. Randevu Kartı Render Fonksiyonu
  const renderAppointmentCard = (appointment: Appointment) => {
    
    let statusStyles = {
      borderColor: 'border-yellow-500', 
      bgColor: 'bg-yellow-100',
      textColor: 'text-yellow-800',
      text: 'Onay Bekliyor'
    };

    if (appointment.status === AppointmentStatus.CONFIRMED) {
      statusStyles = { borderColor: 'border-green-500', bgColor: 'bg-green-100', textColor: 'text-green-800', text: 'Onaylandı' };
    } else if (appointment.status === AppointmentStatus.CANCELLED) {
      statusStyles = { borderColor: 'border-red-500', bgColor: 'bg-red-100', textColor: 'text-red-800', text: 'İptal Edildi' };
    } else if (appointment.status === AppointmentStatus.COMPLETED) {
      statusStyles = { borderColor: 'border-gray-500', bgColor: 'bg-gray-100', textColor: 'text-gray-800', text: 'Tamamlandı' };
    }
    
    return (
      <div key={appointment.id} className={`bg-white p-5 rounded-lg shadow-md border-l-4 ${statusStyles.borderColor}`}>
        <div className="flex justify-between items-start mb-3">
          <div className="flex items-center space-x-3">
            <div className="w-12 h-12 rounded-full bg-gray-100 flex items-center justify-center">
              <UserCircle className="w-8 h-8 text-gray-400" />
            </div>
            <div>
              <h3 className="text-lg font-semibold text-gray-900">{appointment.name}</h3>
              <p className="text-sm text-gray-500">
                {format(new Date(appointment.dateTime), 'dd MMMM yyyy - HH:mm', { locale: tr })}
              </p>
            </div>
          </div>
          <span className={`px-3 py-1 text-xs font-medium ${statusStyles.textColor} ${statusStyles.bgColor} rounded-full`}>
            {statusStyles.text}
          </span>
        </div>
        
        <div className="space-y-2 mb-4">
          <p className="flex items-center text-sm text-gray-600">
            <Phone className="w-4 h-4 mr-2 text-gray-400" /> {appointment.phone || 'Belirtilmemiş'}
          </p> 
          <p className="flex items-center text-sm text-gray-600">
            <Mail className="w-4 h-4 mr-2 text-gray-400" /> {appointment.email}
          </p> 
        </div>

        <div className="flex justify-end space-x-2 pt-4 border-t mt-4">
          
          {appointment.status === AppointmentStatus.PENDING && (
            <>
              <button 
                onClick={() => handleUpdateStatus(appointment.id, AppointmentStatus.CANCELLED)}
                className="flex items-center px-3 py-2 text-sm font-medium text-red-600 bg-red-50 rounded-lg hover:bg-red-100"
              >
                <X className="w-4 h-4 mr-1" /> İptal Et
              </button>
              <button 
                onClick={() => handleUpdateStatus(appointment.id, AppointmentStatus.CONFIRMED)}
                className="flex items-center px-3 py-2 text-sm font-medium text-white bg-green-600 rounded-lg hover:bg-green-700"
              >
                <Check className="w-4 h-4 mr-1" /> Onayla
              </button>
            </>
          )}

          {appointment.status === AppointmentStatus.CONFIRMED && (
             <button 
                onClick={() => handleUpdateStatus(appointment.id, AppointmentStatus.COMPLETED)}
                className="flex items-center px-3 py-2 text-sm font-medium text-white bg-indigo-600 rounded-lg hover:bg-indigo-700"
              >
                <CheckCircle className="w-4 h-4 mr-1" /> Tamamlandı Olarak İşaretle
              </button>
          )}

          {(appointment.status === AppointmentStatus.COMPLETED || appointment.status === AppointmentStatus.CANCELLED) && (
            <p className="text-sm text-gray-500 italic">Bu randevu sonuçlandı.</p>
          )}
        </div>
      </div>
    );
  };

  // 7. Ana JSX (Return)
  return (
    <div className="p-8">
      
      <DashboardHeader title="Randevular" />
      
      <div className="mb-6 border-b border-gray-200">
        <nav className="flex space-x-8">
          <a href="#" className="py-4 px-1 border-b-2 border-indigo-600 text-indigo-600 font-semibold">
            Yaklaşan Randevular
          </a>
          <a href="#" className="py-4 px-1 border-b-2 border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700">
            Geçmiş Randevular
          </a>
        </nav>
      </div>

      {isLoading && ( <div className="text-center p-10 text-gray-500">Randevular yükleniyor...</div> )}
      
      {error && (
        <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4" role="alert">
          <p className="font-bold">Hata!</p>
          <p>{error}</p>
        </div>
      )}

      {!isLoading && !error && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {appointments.length === 0 ? (
            <p className="p-10 text-gray-500 col-span-3 text-center">Gösterilecek randevu bulunamadı.</p>
          ) : (
            appointments.map(renderAppointmentCard)
          )}
        </div>
      )}
      
    </div>
  );
}